define(["../entity"], function (Entity) {
  return Entity.extend({
    init: function init(id, kind, count, ability, abilityLevel) {
      var self = this;

      self._super(id, kind);

      self.count = count;
      self.ability = ability;
      self.abilityLevel = abilityLevel;
      self.dropped = false;
      self.stackable = false;
      self.type = "item";
    },
    idle: function idle() {
      this.setAnimation("idle", 150);
    },
    setName: function setName(name) {
      this._super(name);
    },
    setAnimation: function setAnimation(name, speed, count) {
      this._super(name, speed, count);
    },
    setGridPosition: function setGridPosition(x, y) {
      this._super(x, y);
    },
    setSprite: function setSprite(sprite) {
      this._super(sprite);
    },
    hasShadow: function hasShadow() {
      return true;
    }
  });
});