define(["../entity"], function (Entity) {
  return Entity.extend({
    init: function init(id, kind) {
      var self = this;

      self._super(id, kind);

      self.type = "chest";
    },
    idle: function idle() {
      this.setAnimation("idle_down", 150);
    },
    setName: function setName(name) {
      this._super(name);
    },
    setAnimation: function setAnimation(name, speed, count, onEndCount) {
      this._super(name, speed, count, onEndCount);
    },
    setGridPosition: function setGridPosition(x, y) {
      this._super(x, y);
    },
    setSprite: function setSprite(sprite) {
      this._super(sprite);
    }
  });
});