define(function () {
  /**
   * The children of these classes are responsible for
   * clear and concise ways of organizing stats of weapons
   * in the client side. This does not dictate the damage,
   * defense or bonus stats, it's just for looks.
   */
  return Class.extend({
    init: function init(name, string, count, ability, abilityLevel) {
      var self = this;
      self.name = name;
      self.string = string;
      self.count = count;
      self.ability = ability;
      self.abilityLevel = abilityLevel;
    },
    exists: function exists() {
      return this.name !== null && this.name !== "null";
    },
    getName: function getName() {
      return this.name;
    },
    getString: function getString() {
      return this.string;
    },
    getCount: function getCount() {
      return this.count;
    },
    getAbility: function getAbility() {
      return this.ability;
    },
    getAbilityLevel: function getAbilityLevel() {
      return this.abilityLevel;
    },
    update: function update(name, string, count, ability, abilityLevel) {
      var self = this;
      self.name = name;
      self.string = string;
      self.count = count;
      self.ability = ability;
      self.abilityLevel = abilityLevel;
    }
  });
});