/* global Packets, Modules, log */
define(["jquery"], function ($) {
  return Class.extend({
    init: function init(game) {
      var self = this;
      self.game = game;
      self.chat = $("#chat");
      self.log = $("#chatLog");
      self.input = $("#hud-chat-input");
      self.button = $("#hud-chat");
      self.visible = false;
      self.fadingDuration = 5000;
      self.fadingTimeout = null;
      self.button.click(function () {
        self.button.blur();
        if (self.input.is(":visible")) self.hideInput();else self.toggle();
      });
    },
    add: function add(source, text, labelColor, textColor) {
      styleLabel = labelColor ? labelColor : "white";
      styleText = textColor ? textColor : "white";
      var self = this,
          element = $('<p><span style="color:' + styleLabel + '">' + source + ':</span><span style="color: ' + styleText + '"> ' + text + "</span></p>");
      self.showChat();
      if (!self.isActive()) self.hideInput();
      self.hideChat();
      self.log.append(element);
      self.log.scrollTop(99999);
    },
    key: function key(data) {
      var self = this;

      switch (data) {
        case Modules.Keys.Enter:
          if (self.input.val() === "") self.toggle();else self.send();
          break;
      }
    },
    send: function send() {
      var self = this;
      self.game.socket.send(Packets.Chat, [self.input.val()]);
      self.toggle();
    },
    toggle: function toggle() {
      var self = this;
      self.clean();
      if (self.visible && !self.isActive()) self.showInput();else if (self.visible) {
        self.hideInput();
        self.hideChat();
      } else {
        self.showChat();
        self.showInput();
      }
    },
    showChat: function showChat() {
      var self = this;
      self.chat.fadeIn("fast");
      self.visible = true;
    },
    showInput: function showInput() {
      var self = this;
      self.button.addClass("active");
      self.input.fadeIn("fast");
      self.input.val("");
      self.input.focus();
      self.clean();
    },
    hideChat: function hideChat() {
      var self = this;

      if (self.fadingTimeout) {
        clearTimeout(self.fadingTimeout);
        self.fadingTimeout = null;
      }

      self.fadingTimeout = setTimeout(function () {
        if (!self.isActive()) {
          self.chat.fadeOut("slow");
          self.visible = false;
        }
      }, self.fadingDuration);
    },
    hideInput: function hideInput() {
      var self = this;
      self.button.removeClass("active");
      self.input.val("");
      self.input.fadeOut("fast");
      self.input.blur();
      self.hideChat();
    },
    clean: function clean() {
      var self = this;
      clearTimeout(self.fadingTimeout);
      self.fadingTimeout = null;
    },
    isActive: function isActive() {
      return this.input.is(":focus");
    }
  });
});