define(function () {
  return Class.extend({
    init: function init(index) {
      var self = this;
      self.index = index;
      self.string = null;
      self.count = -1;
      self.ability = -1;
      self.abilityLevel = -1;
      self.edible = false;
      self.equippable = false;
    },
    load: function load(string, count, ability, abilityLevel, edible, equippable) {
      var self = this;
      self.string = string;
      self.count = count;
      self.ability = ability;
      self.abilityLevel = abilityLevel;
      self.edible = edible;
      self.equippable = equippable;
    },
    empty: function empty() {
      var self = this;
      self.string = null;
      self.count = -1;
      self.ability = -1;
      self.abilityLevel = -1;
      self.edible = false;
      self.equippable = false;
    },
    isEmpty: function isEmpty() {
      return this.string === null || this.count === -1;
    },
    setCount: function setCount(count) {
      this.count = count;
    }
  });
});