define(["../character"], function (Character) {
  return Character.extend({
    init: function init(id, kind) {
      var self = this;

      self._super(id, kind);

      self.name = name;
      self.hitPoints = -1;
      self.maxHitPoints = -1;
      self.type = "mob";
    },
    setHitPoints: function setHitPoints(hitPoints) {
      this._super(hitPoints);
    },
    setMaxHitPoints: function setMaxHitPoints(maxHitPoints) {
      this._super(maxHitPoints);
    },
    idle: function idle() {
      this._super();
    },
    performAction: function performAction(orientation, action) {
      this._super(orientation, action);
    },
    setSprite: function setSprite(sprite) {
      this._super(sprite);
    },
    setName: function setName(name) {
      this.name = name;
    },
    setGridPosition: function setGridPosition(x, y) {
      this._super(x, y);
    }
  });
});