define(["./equipment"], function (Equipment) {
  return Equipment.extend({
    init: function init(name, string, count, ability, abilityLevel) {
      var self = this;

      self._super(name, string, count, ability, abilityLevel);

      self.level = -1;
      self.damage = -1;
      self.ranged = false;
    },
    exists: function exists() {
      return this._super();
    },
    setDamage: function setDamage(damage) {
      this.damage = damage;
    },
    setLevel: function setLevel(level) {
      this.level = level;
    },
    getDamage: function getDamage() {
      return this.damage;
    },
    getLevel: function getLevel() {
      return this.level;
    },
    getString: function getString() {
      return this._super();
    },
    update: function update(name, string, count, ability, abilityLevel) {
      this._super(name, string, count, ability, abilityLevel);
    }
  });
});